import React, { useState } from "react";
import { Link } from "react-router-dom";
import { links } from "./nav.links";
import { NavButtons } from "./navbuttons.component";
import { triangle_up } from "../../assets/images";

export const NavigationLinks = () => {

  const [heading, setHeading] = useState("");
  const [subHeading, setSubHeading] = useState("");
  const [showHeading, setShowHeading] = useState(false)

  const toggleMenu = () => {
    console.log(showHeading)
    setShowHeading(!showHeading)
  }
  return (
    <>
      {links.map((link) => (
        <div>
          <div className="px-30 text-right md:cursor-pointer group">
            <NavButtons onClick = {toggleMenu} />
            {showHeading && (
              <div className="absolute p-4 top-[60px] right-10 z-index-40">
                <div className="bg-[#f4f4f4] rounded-lg p-10 px-10 grid grid-cols-3 gap-2">
                  {link.sublinks.map((mysublinks, index) => (
                    <div className="text-left" key={index}>
                      <div className="pb-1 border-black flex font-light items-center text-[#000e14]" style={{borderBottomWidth: 1}}>
                        {mysublinks.icon}&nbsp;
                        <span>{mysublinks.Head}</span> &nbsp;
                        <span className="font-semibold">{mysublinks.subHead}</span>&nbsp;&nbsp;&nbsp;
                      </div>

                      <ul className="text-left">
                      {mysublinks.sublink.map((slink, index) => {
                        return (
                          <li className="text-black my-1 font-semibold text-xs" key={index}>
                            <Link to={slink.link} className="hover:text-blue">
                              {slink.name}
                            </Link>
                          </li>
                        )
                      })}
                      </ul>
                    </div>
                  ))}
                </div>
              </div>
            )}
            {showHeading && (
                <img src={triangle_up} style={{width: 79, height: 30}} className="absolute top-[65px] right-[55px] z-index-40" />
            )}
          </div>
          {/* Mobile menus */}
          <div
            className={`
              ${heading === link.name ? "md:hidden" : "hidden"}
            `}
          >
            {/* sublinks */}
            {link.sublinks.map((slinks) => (
              <div>
                <div>
                  <h1
                    onClick={() =>
                      subHeading !== slinks.Head
                        ? setSubHeading(slinks.Head)
                        : setSubHeading("")
                    }
                    className="text-black py-4 pl-7 font-semibold md:pr-0 pr-5 flex justify-between items-center md:pr-0 pr-5"
                  >
                    {slinks.Head}

                    <span className="text-xl md:mt-1 md:ml-2 inline">
                      <ion-icon
                        name={`${subHeading === slinks.Head
                            ? "chevron-up"
                            : "chevron-down"
                          }`}
                      ></ion-icon>
                    </span>
                  </h1>
                  <div
                    className={`${subHeading === slinks.Head ? "md:hidden" : "hidden"
                      }`}
                  >
                    {slinks.sublink.map((slink) => (
                      <li className="py-3 pl-14">
                        <Link to={slink.link}>{slink.name}</Link>
                      </li>
                    ))}
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      ))}
    </>
  );
};

